body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(200, 211, 219);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* text-align: center; */
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.App-header {
  text-align: center;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 10px;
}

.headline-area {
  box-sizing: border-box;
  flex: 1 1 auto;
  flex-basis: 800px;
  padding: 15px;
  overflow: auto;
  width: 100%;
  max-width: 800px;
  margin: auto;
}

form {
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin: auto;
  display: flex;
}
input {
  padding: 5px;
  margin: 3px;
}
input[type="text"] {
  flex: 1 1 auto;
}
